html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.container {
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (min-width: 768px){
        padding-left: 75px;
        padding-right: 75px;
    }
    @media screen and (min-width: 1024px){
        padding-left: 71px;
        padding-right: 71px;    }

}
